@import url("https://fonts.googleapis.com/css2?family=Lobster+Two:ital,wght@0,400;1,400;1,700&display=swap");

*,
*::before,
*::after {
	box-sizing: border-box;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: "Lobster Two", cursive, "sans-serif";
	font-size: 1.2rem;
	color: white;
}

@media (max-width: 1200px) {
	body {
		font-size: 1rem;
	}
}

@media (max-width: 900px) {
	body {
		font-size: 0.8rem;
	}
}

@media (max-width: 450px) {
	body {
		font-size: 0.6rem;
	}
}

.container {
	display: flex;
	min-height: 100vh;
}

.app {
	max-width: 1200px;
	max-height: 90vh;
	margin: 0 auto;
	align-self: center;
	box-shadow: 0px 5px 16px #3f3d56;
	background-color: #e5e5e5;
	display: flex;
	flex-direction: column;
	flex: 1;
	/* dla ostylowania .square */
	position: relative;
}

@media (max-width: 600px) {
	.app {
		min-height: 100vh;
	}
}
