.form {
	display: flex;
	padding: 1rem;
	align-items: center;
	justify-content: space-between;
}

@media (max-width: 700px) {
	.form {
		padding: 0.5rem;
		flex-wrap: wrap;
		justify-content: center;
	}
}

@media (min-width: 1000px) {
	input[type="text"] {
		margin-left: 7rem;
	}
}

input[type="text"],
input[type="date"] {
	font-size: inherit;
	font-family: "Lobster Two", cursive, "sans-serif";
	background-color: #3f3d56;
	color: #f2f2f2;
	border: 4px solid #3f3d56;
	padding: 0.4rem 1.2rem;
	box-shadow: 0px 5px 12px rgba(0, 0, 0, 0.25);
	border-radius: 20px;
	line-height: 100%;
	margin-right: 1.8rem;
	height: 2.2rem;
	outline: none;
	transition: all 0.2s;
	font-style: italic;
	text-align: center;
}

@media (max-width: 900px) {
	input[type="text"],
	input[type="date"] {
		margin-right: 0.5rem;
		/* height: 1.8rem; */
		/* margin-bottom: 5px; */
		border: 5px solid #e5e5e5;
		box-shadow: none;
	}
}

::-webkit-input-placeholder {
	color: #f2f2f2;
	opacity: 1;
	font-size: 1rem;
}

:-ms-input-placeholder {
	color: #f2f2f2;
	opacity: 1;
	font-size: 1rem;
}

::-ms-input-placeholder {
	color: #f2f2f2;
	opacity: 1;
	font-size: 1rem;
}

::placeholder,
input[type="date"] {
	color: #f2f2f2;
	opacity: 1;
	font-size: 1rem;
}

@media (max-width: 900px) {
	::-webkit-input-placeholder {
		font-size: 0.8rem;
	}
	:-ms-input-placeholder {
		font-size: 0.8rem;
	}
	::-ms-input-placeholder {
		font-size: 0.8rem;
	}
	::placeholder,
	input[type="date"] {
		font-size: 0.8rem;
	}
}

input[type="date"] {
	margin-right: 3rem;
}

@media (max-width: 900px) {
	input[type="date"] {
		margin-right: 1rem;
	}
}

input[type="checkbox"] {
	margin-right: 0.8rem;
}

label {
	color: #2f2e41;
	margin-right: 1rem;
	font-style: italic;
}

@media (max-width: 450px) {
	label {
		font-size: 0.8rem;
	}
}

label:nth-of-type(2) {
	margin-right: 0.8rem;
}

.form button {
	font-style: normal;
	font-weight: normal;
	font-size: 1.1rem;
	/* line-height: 30px; */
	width: 8rem;
	height: 2rem;
	outline: none;
	border: none;
	cursor: pointer;
	transition: 0.2s;
	color: #2f2e41;
	background: #f2f2f2;
	box-shadow: 0px 5px 12px rgba(0, 0, 0, 0.25);
	border-radius: 20px;
	text-transform: uppercase;
	font-family: inherit;
	padding: 0.2rem 1.2rem;
	text-align: center;
	margin-right: 2rem;
}

@media (max-width: 700px) {
	.form button {
		font-size: 1rem;
		width: 8rem;
		height: 1.6rem;
		padding: 0.2rem 1rem;
	}
}

@media (max-width: 700px) {
	.form button {
		margin-left: 1rem;
	}
}

@media (max-width: 400px) {
	.form button {
		margin-left: 1rem;
		margin-top: 1rem;
	}
}

.form button:hover {
	background-color: #57b894;
	color: #f2f2f2;
}

input[type="text"]:hover,
input[type="date"]:hover,
.form button:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
	cursor: pointer;
}

/* stylowanie alertu dla zadania */
h3 {
	color: #3f3d56;
	margin-top: 1rem;
}

nav ul {
	display: flex;
	list-style: none;
	margin-left: 2rem;
}

@media (max-width: 600px) {
	nav ul {
		margin-left: 0.2rem;
	}
}

nav a {
	display: block;
	color: #3f3d56;
	padding: 1rem;
	text-decoration: none;
	cursor: pointer;
	font-style: italic;
	font-size: 1.3rem;
}

@media (max-width: 900px) {
	nav a {
		padding: 0.8rem;
		font-size: 1rem;
	}
}

@media (max-width: 600px) {
	nav a {
		padding: 0.5rem;
		font-size: 0.8rem;
	}
}

nav a:hover {
	color: #57b894;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: "Lobster Two", cursive, "sans-serif";
	font-size: 1.2rem;
	color: white;
}

@media (max-width: 1200px) {
	body {
		font-size: 1rem;
	}
}

@media (max-width: 900px) {
	body {
		font-size: 0.8rem;
	}
}

@media (max-width: 450px) {
	body {
		font-size: 0.6rem;
	}
}

.container {
	display: flex;
	min-height: 100vh;
}

.app {
	max-width: 1200px;
	max-height: 90vh;
	margin: 0 auto;
	align-self: center;
	box-shadow: 0px 5px 16px #3f3d56;
	background-color: #e5e5e5;
	display: flex;
	flex-direction: column;
	flex: 1 1;
	/* dla ostylowania .square */
	position: relative;
}

@media (max-width: 600px) {
	.app {
		min-height: 100vh;
	}
}

section {
    display: flex;
}

aside {
    display: flex;
    flex-basis: 30%;
    flex-direction: column;
    overflow: hidden;
    align-content: stretch;
    justify-content: space-between;
}

@media(max-width: 1100px) {
    aside {
        flex-basis: 25%;
    }
}

@media(max-width: 700px) {
    aside {
        flex-basis: 15%;
    }
}

@media(max-width: 600px) {
    aside {
        flex-basis: 10%;
    }
}

main {
    position: relative;
    z-index: 2;
    width: 90%;
}

.img {
    height: 44vh;
}

.logo {
    width: 12rem;
    height: 6rem;
}

@media(max-width: 900px) {
    .logo {
        position: absolute;
        top: 6rem;
        left: -1rem;
        width: 8rem;
        height: 4rem;
    }
}

@media(max-width: 600px) {
    .logo {
        display: none;
    }

}

@media(max-width: 600px) {

    .img {
        display: none;
    }
}

.img {
    height: 44vh;
}


table {
    margin-top: 2rem;
    width: 90%;
    font-style: italic;
    font-weight: normal;
    border-collapse: collapse;
    table-layout: fixed;
}

td {
    letter-spacing: 1px;
    padding: 5px;
}

tbody td {
    text-align: center;
}


thead>tr {
    line-height: 3rem;
    color: #57B894;
    font-size: 1.3rem;
}

@media(max-width:900px) {
    thead>tr {
        font-size: 1rem;
    }
}

@media(max-width:500px) {
    thead>tr {
        font-size: .8rem;
    }
}

tbody>tr>th {
    text-align: left;
    padding-left: 0.5rem;
}

td {
    text-align: center;
}

tbody>tr:nth-of-type(1n) {
    background-color: #E5E5E5;
    color: #3f3d56;
}

tbody>tr:nth-of-type(2n) {
    background-color: #3f3d56;
    opacity: 0.85;
    color: #E5E5E5;
}

/* stylowanie tabeli tbl-1 */
.tbl-1 thead {
    background-color: #3F3D56;

}

.tbl-1 thead th:nth-child(1) {
    width: 50%;
    text-align: left;
    padding-left: 0.5rem;
}

.tbl-1 thead th:nth-child(2) {
    width: 20%;
}

.tbl-1 thead th:nth-child(3) {
    width: 10%;
}

.tbl-1 thead th:nth-child(4) {
    width: 10%;
}

/* stylowanie tabeli tbl-2 */

.tbl-2 thead {
    background-color: #3F3D56;
}

.tbl-2 thead th:nth-child(1) {
    width: 46%;
    text-align: left;
    padding-left: 0.5rem;
}

.tbl-2 thead th:nth-child(2) {
    width: 22%;
}

.tbl-2 thead th:nth-child(3) {
    width: 22%;
}

.tbl-2 thead th:nth-child(4) {
    width: 10%;
}

@media(max-width: 600px) {

    .tbl-2 thead>tr {
        line-height: 2rem;
        font-size: .8rem;
    }
}

@media(max-width: 500px) {
    .tbl-2 thead th:nth-child(1) {
        width: 40%;
    }

    .tbl-2 thead>tr {
        line-height: 2rem;
        font-size: .7rem;
    }
}

/* stylowanie przycisków typu svg */
button {
    background-color: inherit;
    outline: none;
    border: none;
    cursor: pointer;
    transition: 0.2s;
    line-height: 100%;
}

button:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

/* stylowanie kwadratu w tle */

.square {
    position: absolute;
    left: -2.4%;
    right: 10.22%;
    top: 7.49%;
    bottom: 10.6%;
    background: #F2F2F2;
    border-radius: 20px;
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
    z-index: -1;
}
footer small {
    color: #3f3d56;
    font-size: .6rem;
    padding: .5rem;
    margin-right: 2.5rem;
    display: flex;
    justify-content: flex-end;
}

@media(max-width: 600px) {
    footer small {
        font-size: .4rem;
    }
}
